"use strict";

exports.pureE = a => () => a;

exports.bindE = a => f => () => f(a())();

exports.untilE = f => () => {
  while (!f());
  return {};
};

exports.whileE = f => a => () => {
  while (f()) {
    a();
  }
  return {};
};

exports.forE = lo => hi => f => () => {
  for (var i = lo; i < hi; i++) {
    f(i)();
  }
};

exports.foreachE = as => f => () => {
  for (var i = 0, l = as.length; i < l; i++) {
    f(as[i])();
  }
};
