"use strict";

exports._head = doc => () => doc.head;

exports._body = doc => () => doc.body;

exports._readyState = doc => () => doc.readyState;

exports._activeElement = doc => () => doc.activeElement;

exports._currentScript = doc => () => doc.currentScript;

exports.referrer = doc => () => doc.referrer;

exports.title = doc => () => doc.title;

exports.setTitle = title => doc => () => {
  doc.title = title;
  return {};
};
