"use strict";

exports.unfoldrArrayImpl = isNothing => fromJust => fst => snd => f => b => {
  var result = [];
  var value = b;
  while (true) { // eslint-disable-line no-constant-condition
    var maybe = f(value);
    if (isNothing(maybe)) return result;
    var tuple = fromJust(maybe);
    result.push(fst(tuple));
    value = snd(tuple);
  }
};
