"use strict";
/* global Symbol */

var hasArrayFrom = typeof Array.from === "function";
var hasStringIterator =
  typeof Symbol !== "undefined" &&
  Symbol != null &&
  typeof Symbol.iterator !== "undefined" &&
  typeof String.prototype[Symbol.iterator] === "function";
var hasFromCodePoint = typeof String.prototype.fromCodePoint === "function";
var hasCodePointAt = typeof String.prototype.codePointAt === "function";

exports._unsafeCodePointAt0 = fallback => hasCodePointAt
  ? str => str.codePointAt(0)
  : fallback;

exports._codePointAt = fallback => Just => Nothing => unsafeCodePointAt0 => index => str => {
  var length = str.length;
  if (index < 0 || index >= length) return Nothing;
  if (hasStringIterator) {
    var iter = str[Symbol.iterator]();
    for (var i = index;; --i) {
      var o = iter.next();
      if (o.done) return Nothing;
      if (i === 0) return Just(unsafeCodePointAt0(o.value));
    }
  }
  return fallback(index)(str);
};

exports._countPrefix = fallback => unsafeCodePointAt0 => {
  if (hasStringIterator) {
    return pred => str => {
      var iter = str[Symbol.iterator]();
      for (var cpCount = 0; ; ++cpCount) {
        var o = iter.next();
        if (o.done) return cpCount;
        var cp = unsafeCodePointAt0(o.value);
        if (!pred(cp)) return cpCount;
      }
    };
  }
  return fallback;
};

exports._fromCodePointArray = singleton => hasFromCodePoint
  ? cps => {
    // Function.prototype.apply will fail for very large second parameters,
    // so we don't use it for arrays with 10,000 or more entries.
    if (cps.length < 10e3) {
      return String.fromCodePoint.apply(String, cps);
    }
    return cps.map(singleton).join("");
  }
  : cps => cps.map(singleton).join("");

exports._singleton = fallback => hasFromCodePoint ? String.fromCodePoint : fallback;

exports._take = fallback => n => {
  if (hasStringIterator) {
    return str => {
      var accum = "";
      var iter = str[Symbol.iterator]();
      for (var i = 0; i < n; ++i) {
        var o = iter.next();
        if (o.done) return accum;
        accum += o.value;
      }
      return accum;
    };
  }
  return fallback(n);
};

exports._toCodePointArray = fallback => unsafeCodePointAt0 => {
  if (hasArrayFrom) {
    return str => Array.from(str, unsafeCodePointAt0);
  }
  return fallback;
};
