"use strict";

exports["showRegex'"] = r => "" + r;

exports["regex'"] = left => right => s1 => s2 => {
  try {
    return right(new RegExp(s1, s2));
  } catch (e) {
    return left(e.message);
  }
};

exports.source = r => r.source;

exports["flags'"] = r => ({
  multiline: r.multiline,
  ignoreCase: r.ignoreCase,
  global: r.global,
  sticky: !!r.sticky,
  unicode: !!r.unicode
});

exports.test = r => s => {
  var lastIndex = r.lastIndex;
  var result = r.test(s);
  r.lastIndex = lastIndex;
  return result;
};

exports._match = just => nothing => r => s => {
  var m = s.match(r);
  if (m == null || m.length === 0) {
    return nothing;
  } else {
    for (var i = 0; i < m.length; i++) {
      m[i] = m[i] == null ? nothing : just(m[i]);
    }
    return just(m);
  }
};

exports.replace = r => s1 => s2 => s2.replace(r, s1);

exports["replace'"] = r => f => s2 => s2.replace(r, function (match) {
  return f(match)(Array.prototype.splice.call(arguments, 1, arguments.length - 3));
});

exports._search = just => nothing => r => s => {
  var result = s.search(r);
  return result === -1 ? nothing : just(result);
};

exports.split = r => s => s.split(r);
