"use strict";

exports.showErrorImpl = err => err.stack || err.toString();

exports.error = msg => new Error(msg);

exports.message = e => e.message;

exports.name = e => e.name || "Error";

exports.stackImpl = just => nothing => e => e.stack ? just(e.stack) : nothing;

exports.throwException = e => () => {
  throw e;
};

exports.catchException = c => t => () => {
  try {
    return t();
  } catch (e) {
    if (e instanceof Error || Object.prototype.toString.call(e) === "[object Error]") {
      return c(e)();
    } else {
      return c(new Error(e.toString()))();
    }
  }
};
