"use strict";

exports.foldrArray = f => init => xs => {
  var acc = init;
  var len = xs.length;
  for (var i = len - 1; i >= 0; i--) {
    acc = f(xs[i])(acc);
  }
  return acc;
};

exports.foldlArray = f => init => xs => {
  var acc = init;
  var len = xs.length;
  for (var i = 0; i < len; i++) {
    acc = f(acc)(xs[i]);
  }
  return acc;
};
