"use strict";

exports.map_ = f => a => () => f(a());

exports.pure_ = a => () => a;

exports.bind_ = a => f => () => f(a())();

exports.run = f => f();

exports["while"] = f => a => () => {
  while (f()) {
    a();
  }
};

exports["for"] = lo => hi => f => () => {
  for (var i = lo; i < hi; i++) {
    f(i)();
  }
};

exports.foreach = as => f => () => {
  for (var i = 0, l = as.length; i < l; i++) {
    f(as[i])();
  }
};

exports.new = val => () => ({
  value: val
});

exports.read = ref => () => ref.value;

exports["modify'"] = f => ref => () => {
  var t = f(ref.value);
  ref.value = t.state;
  return t.value;
};

exports.write = a => ref => () => // eslint-disable-line no-return-assign
ref.value = a;
