"use strict";

// module Data.Function.Uncurried

exports.mkFn0 = fn => () => fn({});

exports.mkFn2 = fn => /* jshint maxparams: 2 */
(a, b) => fn(a)(b);

exports.mkFn3 = fn => /* jshint maxparams: 3 */
(a, b, c) => fn(a)(b)(c);

exports.mkFn4 = fn => /* jshint maxparams: 4 */
(a, b, c, d) => fn(a)(b)(c)(d);

exports.mkFn5 = fn => /* jshint maxparams: 5 */
(a, b, c, d, e) => fn(a)(b)(c)(d)(e);

exports.mkFn6 = fn => /* jshint maxparams: 6 */
(a, b, c, d, e, f) => fn(a)(b)(c)(d)(e)(f);

exports.mkFn7 = fn => /* jshint maxparams: 7 */
(a, b, c, d, e, f, g) => fn(a)(b)(c)(d)(e)(f)(g);

exports.mkFn8 = fn => /* jshint maxparams: 8 */
(a, b, c, d, e, f, g, h) => fn(a)(b)(c)(d)(e)(f)(g)(h);

exports.mkFn9 = fn => /* jshint maxparams: 9 */
(a, b, c, d, e, f, g, h, i) => fn(a)(b)(c)(d)(e)(f)(g)(h)(i);

exports.mkFn10 = fn => /* jshint maxparams: 10 */
(a, b, c, d, e, f, g, h, i, j) => fn(a)(b)(c)(d)(e)(f)(g)(h)(i)(j);

exports.runFn0 = fn => fn();

exports.runFn2 = fn => a => b => fn(a, b);

exports.runFn3 = fn => a => b => c => fn(a, b, c);

exports.runFn4 = fn => a => b => c => d => fn(a, b, c, d);

exports.runFn5 = fn => a => b => c => d => e => fn(a, b, c, d, e);

exports.runFn6 = fn => a => b => c => d => e => f => fn(a, b, c, d, e, f);

exports.runFn7 = fn => a => b => c => d => e => f => g => fn(a, b, c, d, e, f, g);

exports.runFn8 = fn => a => b => c => d => e => f => g => h => fn(a, b, c, d, e, f, g, h);

exports.runFn9 = fn => a => b => c => d => e => f => g => h => i => fn(a, b, c, d, e, f, g, h, i);

exports.runFn10 = fn => a => b => c => d => e => f => g => h => i => j => fn(a, b, c, d, e, f, g, h, i, j);
