"use strict";

var ReactDOM = require("react-dom");

exports.render_ = (jsx, node, callback) => {
  ReactDOM.render(jsx, node, callback);
};

exports.hydrate_ = (jsx, node, callback) => {
  ReactDOM.hydrate(jsx, node, callback);
};

exports.unmountComponentAtNode_ = node => ReactDOM.unmountComponentAtNode(node);

exports.findDOMNode_ = instance => ReactDOM.findDOMNode(instance);

exports.createPortal_ = (jsx, node) => ReactDOM.createPortal(jsx, node);
