"use strict";

exports.fromCharArray = a => a.join("");

exports.toCharArray = s => s.split("");

exports.singleton = c => c;

exports._charAt = just => nothing => i => s => i >= 0 && i < s.length ? just(s.charAt(i)) : nothing;

exports._toChar = just => nothing => s => s.length === 1 ? just(s) : nothing;

exports.length = s => s.length;

exports.countPrefix = p => s => {
  var i = 0;
  while (i < s.length && p(s.charAt(i))) i++;
  return i;
};

exports._indexOf = just => nothing => x => s => {
  var i = s.indexOf(x);
  return i === -1 ? nothing : just(i);
};

exports["_indexOf'"] = just => nothing => x => startAt => s => {
  if (startAt < 0 || startAt > s.length) return nothing;
  var i = s.indexOf(x, startAt);
  return i === -1 ? nothing : just(i);
};

exports._lastIndexOf = just => nothing => x => s => {
  var i = s.lastIndexOf(x);
  return i === -1 ? nothing : just(i);
};

exports["_lastIndexOf'"] = just => nothing => x => startAt => s => {
  if (startAt < 0 || startAt > s.length) return nothing;
  var i = s.lastIndexOf(x, startAt);
  return i === -1 ? nothing : just(i);
};

exports.take = n => s => s.substr(0, n);

exports.drop = n => s => s.substring(n);

exports._slice = b => e => s => s.slice(b,e);

exports.splitAt = i => s => ({
  before: s.substring(0, i),
  after: s.substring(i)
});
