"use strict";

exports.fromNumberImpl = just => nothing => n => /* jshint bitwise: false */
(n | 0) === n ? just(n) : nothing;

exports.toNumber = n => n;

exports.fromStringAsImpl = just => nothing => radix => {
  var digits;
  if (radix < 11) {
    digits = "[0-" + (radix - 1).toString() + "]";
  } else if (radix === 11) {
    digits = "[0-9a]";
  } else {
    digits = "[0-9a-" + String.fromCharCode(86 + radix) + "]";
  }
  var pattern = new RegExp("^[\\+\\-]?" + digits + "+$", "i");

  return s => {
    /* jshint bitwise: false */
    if (pattern.test(s)) {
      var i = parseInt(s, radix);
      return (i | 0) === i ? just(i) : nothing;
    } else {
      return nothing;
    }
  };
};

exports.toStringAs = radix => i => i.toString(radix);


exports.quot = x => y => /* jshint bitwise: false */
x / y | 0;

exports.rem = x => y => x % y;

exports.pow = x => y => /* jshint bitwise: false */
Math.pow(x,y) | 0;
