"use strict";

exports.unfoldr1ArrayImpl = isNothing => fromJust => fst => snd => f => b => {
  var result = [];
  var value = b;
  while (true) { // eslint-disable-line no-constant-condition
    var tuple = f(value);
    result.push(fst(tuple));
    var maybe = snd(tuple);
    if (isNothing(maybe)) return result;
    value = fromJust(maybe);
  }
};
