"use strict";

exports._copyST = m => () => {
  var r = {};
  for (var k in m) {
    if (hasOwnProperty.call(m, k)) {
      r[k] = m[k];
    }
  }
  return r;
};

exports.empty = {};

exports.runST = f => f();

exports._fmapObject = (m0, f) => {
  var m = {};
  for (var k in m0) {
    if (hasOwnProperty.call(m0, k)) {
      m[k] = f(m0[k]);
    }
  }
  return m;
};

exports._mapWithKey = (m0, f) => {
  var m = {};
  for (var k in m0) {
    if (hasOwnProperty.call(m0, k)) {
      m[k] = f(k)(m0[k]);
    }
  }
  return m;
};

exports._foldM = bind => f => mz => m => {
  var acc = mz;
  function g(k) {
    return z => f(z)(k)(m[k]);
  }
  for (var k in m) {
    if (hasOwnProperty.call(m, k)) {
      acc = bind(acc)(g(k));
    }
  }
  return acc;
};

exports._foldSCObject = (m, z, f, fromMaybe) => {
  var acc = z;
  for (var k in m) {
    if (hasOwnProperty.call(m, k)) {
      var maybeR = f(acc)(k)(m[k]);
      var r = fromMaybe(null)(maybeR);
      if (r === null) return acc;
      else acc = r;
    }
  }
  return acc;
};

exports.all = f => m => {
  for (var k in m) {
    if (hasOwnProperty.call(m, k) && !f(k)(m[k])) return false;
  }
  return true;
};

exports.size = m => {
  var s = 0;
  for (var k in m) {
    if (hasOwnProperty.call(m, k)) {
      ++s;
    }
  }
  return s;
};

exports._lookup = (no, yes, k, m) => k in m ? yes(m[k]) : no;

exports._lookupST = (no, yes, k, m) => () => k in m ? yes(m[k]) : no;

function toArrayWithKey(f) {
  return m => {
    var r = [];
    for (var k in m) {
      if (hasOwnProperty.call(m, k)) {
        r.push(f(k)(m[k]));
      }
    }
    return r;
  };
}

exports.toArrayWithKey = toArrayWithKey;

exports.keys = Object.keys || toArrayWithKey(k => () => k);
