"use strict";

var unsafeCompareImpl = lt => eq => gt => x => y => x < y ? lt : x === y ? eq : gt;

exports.ordBooleanImpl = unsafeCompareImpl;
exports.ordIntImpl = unsafeCompareImpl;
exports.ordNumberImpl = unsafeCompareImpl;
exports.ordStringImpl = unsafeCompareImpl;
exports.ordCharImpl = unsafeCompareImpl;

exports.ordArrayImpl = f => xs => ys => {
  var i = 0;
  var xlen = xs.length;
  var ylen = ys.length;
  while (i < xlen && i < ylen) {
    var x = xs[i];
    var y = ys[i];
    var o = f(x)(y);
    if (o !== 0) {
      return o;
    }
    i++;
  }
  if (xlen === ylen) {
    return 0;
  } else if (xlen > ylen) {
    return -1;
  } else {
    return 1;
  }
};
