"use strict";

exports.empty = () => [];

exports.peekImpl = just => nothing => i => xs => () => i >= 0 && i < xs.length ? just(xs[i]) : nothing;

exports.poke = i => a => xs => () => {
  var ret = i >= 0 && i < xs.length;
  if (ret) xs[i] = a;
  return ret;
};

exports.popImpl = just => nothing => xs => () => xs.length > 0 ? just(xs.pop()) : nothing;

exports.pushAll = as => xs => () => xs.push.apply(xs, as);

exports.shiftImpl = just => nothing => xs => () => xs.length > 0 ? just(xs.shift()) : nothing;

exports.unshiftAll = as => xs => () => xs.unshift.apply(xs, as);

exports.splice = i => howMany => bs => xs => () => xs.splice.apply(xs, [i, howMany].concat(bs));

exports.unsafeFreeze = xs => () => xs;

exports.unsafeThaw = xs => () => xs;

function copyImpl(xs) {
  return () => xs.slice();
}

exports.freeze = copyImpl;

exports.thaw = copyImpl;

exports.sortByImpl = comp => xs => () => xs.sort((x, y) => comp(x)(y));

exports.toAssocArray = xs => () => {
  var n = xs.length;
  var as = new Array(n);
  for (var i = 0; i < n; i++) as[i] = { value: xs[i], index: i };
  return as;
};
