"use strict";

exports.charAt = i => s => {
  if (i >= 0 && i < s.length) return s.charAt(i);
  throw new Error("Data.String.Unsafe.charAt: Invalid index.");
};

exports.char = s => {
  if (s.length === 1) return s.charAt(0);
  throw new Error("Data.String.Unsafe.char: Expected string of length 1.");
};
