"use strict";

exports.intDegree = x => Math.min(Math.abs(x), 2147483647);

// See the Euclidean definition in
// https://en.m.wikipedia.org/wiki/Modulo_operation.
exports.intDiv = x => y => {
  if (y === 0) return 0;
  return y > 0 ? Math.floor(x / y) : -Math.floor(x / -y);
};

exports.intMod = x => y => {
  if (y === 0) return 0;
  var yy = Math.abs(y);
  return ((x % yy) + yy) % yy;
};

exports.numDiv = n1 => n2 => n1 / n2;
