"use strict";

exports.unsafeHas = label => rec => ({}).hasOwnProperty.call(rec, label);

exports.unsafeGet = label => rec => rec[label];

exports.unsafeSet = label => value => rec => {
  var copy = {};
  for (var key in rec) {
    if ({}.hasOwnProperty.call(rec, key)) {
      copy[key] = rec[key];
    }
  }
  copy[label] = value;
  return copy;
};

exports.unsafeDelete = label => rec => {
  var copy = {};
  for (var key in rec) {
    if (key !== label && {}.hasOwnProperty.call(rec, key)) {
      copy[key] = rec[key];
    }
  }
  return copy;
};
