"use strict";

exports._localeCompare = lt => eq => gt => s1 => s2 => {
  var result = s1.localeCompare(s2);
  return result < 0 ? lt : result > 0 ? gt : eq;
};

exports.replace = s1 => s2 => s3 => s3.replace(s1, s2);

exports.replaceAll = s1 => s2 => s3 => // eslint-disable-line no-useless-escape
s3.replace(new RegExp(s1.replace(/[-\/\\^$*+?.()|[\]{}]/g, "\\$&"), "g"), s2);

exports.split = sep => s => s.split(sep);

exports.toLower = s => s.toLowerCase();

exports.toUpper = s => s.toUpperCase();

exports.trim = s => s.trim();

exports.joinWith = s => xs => xs.join(s);
